import React, { useEffect, useState } from "react";
import icon from "./images/files.png";
import "./styles/explorer.css";
import Topbar from "./Topbar";
function Explorer({ app }) {
  const [currentfolder, setcurrentfolder] = useState("photos");

  useEffect(() => {
    const folders = document.getElementsByClassName("explorer-sidebar-btns");
    for (const folder of folders) {
      folder.style.transform = `scale(1)`;
    }
    document.getElementById(currentfolder).style.transform = `scale(1.14)`;
  }, [currentfolder]);

  return (
    <div className="all_app" id="explorer_app">
      <Topbar
        app_id={"explorer_app"}
        title={"Files"}
        window_id={app}
        icon={icon}
      />
      <div className="explorer-sidebar">
        <button
          className="explorer-sidebar-btns"
          id="photos"
          onClick={() => setcurrentfolder("photos")}
        >
          <div className="btn-icon" id="photos-icon"></div>
          <div className="btn-title">Photos</div>
        </button>
        <button
          className="explorer-sidebar-btns"
          id="audio"
          onClick={() => setcurrentfolder("audio")}
        >
          <div className="btn-icon" id="audio-icon"></div>
          <div className="btn-title">Musics</div>
        </button>
        <button
          className="explorer-sidebar-btns"
          id="videos"
          onClick={() => setcurrentfolder("videos")}
        >
          <div className="btn-icon" id="videos-icon"></div>
          <div className="btn-title">Videos</div>
        </button>
        <button
          className="explorer-sidebar-btns"
          id="documents"
          onClick={() => setcurrentfolder("documents")}
        >
          <div className="btn-icon" id="documents-icon"></div>
          <div className="btn-title">Documents</div>
        </button>
        <button
          className="explorer-sidebar-btns"
          id="html"
          onClick={() => setcurrentfolder("html")}
        >
          <div className="btn-icon" id="html-icon"></div>
          <div className="btn-title">Web Dev</div>
        </button>
        <button
          className="explorer-sidebar-btns"
          id="python"
          onClick={() => setcurrentfolder("python")}
        >
          <div className="btn-icon" id="python-icon"></div>
          <div className="btn-title">Python</div>
        </button>
      </div>
      <div className="explorer-content"></div>
    </div>
  );
}

export default Explorer;
